import { Component, h, Prop, Element, State, Method } from '@stencil/core';
import { common } from '../../global/data/common';
import InputInterface from './inputInterface';
@Component({
      tag: 'ks-input-textarea',
      styleUrl: 'inputTextarea.css'
})
export class InputTextarea implements InputInterface {
    @Element() root: HTMLElement;

    @Prop() name: string;
    @Prop() placeholder: string;
    @Prop() label: string;
    @Prop() rows: number;

    @Prop() min: number;
    @Prop() max: number;
    @Prop() required: boolean;
    @Prop({reflect: true}) noresize: boolean;

    @Prop({reflect: true, mutable: true}) invalid = false;
    @State() invalidMessage: string = "";

	render() {
		return[
            this.label ? 
                <label>{this.label}</label>
            : null,

			<textarea rows={this.rows} name={this.name} placeholder={this.placeholder} onChange={()=>this.Validate()}>
            </textarea>,
            
            this.invalid ? 
                <p>{this.invalidMessage}</p>
            : null
        ];
    }

    @Method()
    async IsValid() {
        return !this.invalid;
    }

    @Method()
    async Validate() {
        const strings = common.get("translations");
        const input = this.root.querySelector("textarea");

        let valid = true;
        let message = [];

        if(input.value.length < this.min) {
            valid = false;
            message.push(strings.inputMinCharacters.replace('{count}', this.min.toString()));
        }

        if(input.value.length > this.max) {
            valid = false;
            message.push(strings.inputMaxCharacters.replace('{count}', this.max.toString()));
        }

        if(this.required && input.value.length == 0) {
            valid = false;
            message = [strings.inputFieldRequired];
        }

        this.invalid = !valid;
        this.invalidMessage = message.join(' ');

        return Promise.resolve();
    }
}
